.dashboardGrid {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-template-rows: auto;
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  grid-template-areas:
    'active-lesson progress-circle'
    'active-module progress-circle';
}
